.wrapper {
  position: relative;
}

.container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  gap: 15px;
  padding: 0 30px;
  margin-top: 125px;
  padding-bottom: 100px;
}

@media screen and (max-width: 640px) {
  .container {
    width: 100%;
    padding: 0 15px;
    min-width: 600px;
  }
}
